<script>
export default {
    mounted() {
        
            $('.wrap').liMarquee({
                // 默认配置项可选:
                scrollamount: 60,
                // direction: 'left',
                // loop: -1,
                // circular: true,
                // drag: true, 
                // hoverstop: true,
            });
       
    }
}
</script>

<style media="screen" lang="less">
@import "../../assets/css/liMarquee.css";
*{
    margin: 0;
    padding: 0;
}
.wrap{
    width: 90%;  
    margin: 10px auto;
    padding: 0;
    font-size: 0 !important; 
    box-sizing: content-box;
    border: 6px solid #000;
    img{
        width: 200px;
        height: 180px;
        vertical-align: bottom;
        border: 2px solid #000;
    }        
}
</style>



